.ticket {
	height: 660px;
	width: 320px;
	color: $white;
	&__top {
		position: relative;
		height: 65.9%;
		clip-path: url(#ticket-top); }
	&__image {
		@include cover;
		@include coverdiv; }
	&__bottom {
		position: relative;
		background-image: $blueGradient;
		clip-path: url(#ticket-bottom);
		height: 34.1%;
		box-shadow : 7px 7px 5px rgba(#000, 0.65); }
	&__text {
		position: absolute;
		bottom: 17px;
		left: 30px;
		text-align: left;
		font-weight: 700; }
	&__logo {
		position: absolute;
		top: calc(50% - 45px);
		left: 50%;
		transform: translate(-50%, -50%); }
	&__place {
		font-family: $ermilov;
		font-size: 20px;
		letter-spacing: -0.04em; }
	&__time {
		font-size: 18px;
		letter-spacing: -0.025em; } }
