body {
	background: #FFF;
	line-height: 1.4;
	font-family: $geo;
	font-size: 16px;
	color: $black; }

.title {
	font-family: $letterica;
	font-size: 108px;
	line-height: 1;
	letter-spacing: 0.02em;
	@include xl {
		font-size: 90px; }
	@include lg {
		font-size: 70px; }
	@include md {
		font-size: 50px; }
	@include sm {
		font-size: 36px; } }
