.header {
    position: absolute;
    top: 44px;
    left: 0;
    width: 100%;
    z-index: 5;
    &__inner {
        margin-left: calc(50% - 800px);
        margin-right: 30px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        @include xl {
            max-width: 1600px;
            padding: 0 10px;
            margin: 0 auto; } }
    &__logo {
        .logo__text {
            @include sm {
                display: none; } } }
    &__search {
        @include vcenter;
        width: 320px;
        @include lg {
            position: static;
            flex-grow: 1;
            padding: 0 50px;
            transform: none; } }
    &__nav {
        display: flex;
        justify-content: space-between;
        align-items: center;
        @include flex-size(612px);
        @include xl {
            @include flex-size(auto); }
        @include md {
            position: fixed;
            top: 0;
            left: 0;
            height: 100%;
            width: 100%;
            flex-direction: column;
            justify-content: space-around;
            transform: translateX(100%);
            background-image: $mainGradient;
            .is-active & {
                transform: translateX(0);
                z-index: -1; } } }
    &__toggle {
        display: none;
        @include md {
            display: block; } } }


.logo {
    display: flex;
    align-items: center;
    color: $white;
    &_dark {
        color: $black; }
    &__image {
        min-width: 50px;
        @include xl {
            max-width: 80px; }
        @include lg {
            max-width: 50px; }
        img {
            max-width: 100%; } }
    &__text {
        position: relative;
        margin-left: 16px;
        @include lg {
            margin-left: 8px; } }

    &__name {
        font-family: $ermilov;
        font-size: 48px;
        letter-spacing: -0.057em;
        @include lg {
            font-size: 40px; }
        @include md {
            font-size: 30px; } }
    &__descr {
        position: absolute;
        left: 0;
        top: 70px;
        margin-right: -20px;
        font-weight: 300;
        font-size: 24px;
        line-height: 1.1;
        @include lg {
            font-size: 18px;
            top: 50px; }
        @include md {
            display: none; } } }

.nav {
    color: $white;
    // display: inline-block
    // vertical-align: middle
    @include md {
        order: 2; }
    &__list {
        display: flex;
        align-items: center;
        @include r(1700) {
            margin-right: 30px; } }
    &__item {
        &:nth-child(1) {
            margin-right: 45px;
            @include r(1700) {
                margin-right: 30px; }
            @include r(1400) {
                margin-right: 20px; } }
        &:nth-child(2) {
            margin-right: 57px;
            @include r(1700) {
                margin-right: 30px; }
            @include r(1400) {
                margin-right: 20px; } } }

    &__link {
        font-size: 24px;

        &_user-name {}

        &_user-avatar {
            margin-left: auto; } }
    .icon-music-circle {
        font-size: 76px;
        @include r(1700) {
            font-size: 60px; } }
    .icon-flame {
        font-size: 40px;
        @include r(1700) {
 } }            // font-size: 30px
    .icon-crown {
        font-size: 38px;
        @include r(1700) {
            font-size: 30px; } } }

.user-info {
    // display: inline-block
    // vertical-align: middle
    margin-left: auto;
    color: $white;
    @include md {
        margin-right: auto;
        order: 1; }
    &__list {
        display: flex;
        align-items: center;
        justify-content: center; }
    &__item {
        // margin-right: 120px
        // + .user-info__item
 }        //     margin-left: -120px
    &__name {
        font-size: 24px;
        line-height: 1.1;
        font-weight: 300; }
    &__avatar {
        display: block;
        margin-left: 32px;
        height: 88px;
        width: 88px;
        img {
            max-width: 100%; }
        @include xl {
            height: 60px;
            width: 60px;
            margin-left: 24px; } } }

.avatar {
    border-radius: 50%; }

.search {
    position: relative;
    width: 100%;
    &__input {
        width: 100%;
        background-color: transparent;
        border: 3px solid rgba($white, 0.5);
        height: 52px;
        padding: 10px 26px;
        color: $white;
        font-size: 18px;
        border-radius: 26px;
        &:focus {
            border-color: $white; } }
    &__button {
        position: absolute;
        top: 50%;
        right: 20px;
        transform: translateY(-50%);
        background-color: transparent;
        .icon {
            color: $white;
            font-size: 24px; } } }

.social-icon {
    display: inline-block;
    vertical-align: middle;
    height: 64px;
    width: 64px;
    img {
        max-width: 100%; } }

.toggle-button {
    position: relative;
    z-index: 20;
    width: 40px;
    height: 30px;
    display: none;
    @include md {
        display: block; }
    span {
        display: block;
        width: 100%;
        height: 3px;
        width: 30px;
        background: $white;
        margin: 6px auto;
        transition: all 0.3s;
        backface-visibility: hidden; } }

.toggle-button.on {
    span:nth-child(1) {
        transform: rotate(45deg) translate(4px, 10px); }
    span:nth-child(2) {
        opacity: 0; }
    span:nth-child(3) {
        transform: rotate(-45deg) translate(3px, -9px); } }
